@keyframes pulse {
  0%,
  100% {
    opacity: 0.1;
    transform: scale(0.9);
  }
  50% {
    opacity: 0.6;
    transform: scale(1);
  }
}

.dot {
  position: fixed;
  border-radius: 50%;
  pointer-events: none;
  animation: pulse linear infinite;
  z-index: -10;
  background-color: hsl(var(--dot-color));
  transition: background-color 0.3s ease-in-out;
}
